import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
  Box,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  CircularProgress,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { fetchBlock } from '../store/slices/blocksSlice';
import { AppDispatch, RootState } from '../store/store';

const BlockDetails: React.FC = () => {
  const { blockNumber } = useParams<{ blockNumber: string }>();
  const dispatch = useDispatch<AppDispatch>();
  const { currentBlock, loading, error } = useSelector((state: RootState) => state.blocks);

  useEffect(() => {
    if (blockNumber) {
      dispatch(fetchBlock(blockNumber));
    }
  }, [dispatch, blockNumber]);

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box p={3}>
        <Typography color="error">{error}</Typography>
      </Box>
    );
  }

  if (!currentBlock) {
    return (
      <Box p={3}>
        <Typography>Block not found</Typography>
      </Box>
    );
  }

  return (
    <Box p={3}>
      <Typography variant="h4" gutterBottom>
        Block #{currentBlock.number}
      </Typography>
      <TableContainer component={Paper} sx={{ mb: 3 }}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell component="th" scope="row">Hash</TableCell>
              <TableCell>{currentBlock.hash}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">Parent Hash</TableCell>
              <TableCell>{currentBlock.parentHash}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">Timestamp</TableCell>
              <TableCell>{new Date(currentBlock.timestamp).toLocaleString()}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell component="th" scope="row">Author</TableCell>
              <TableCell>{currentBlock.author}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>

      <Accordion>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography>Extrinsics ({currentBlock.extrinsics.length})</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <pre>{JSON.stringify(currentBlock.extrinsics, null, 2)}</pre>
        </AccordionDetails>
      </Accordion>

      <Accordion>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography>Events ({currentBlock.events.length})</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <pre>{JSON.stringify(currentBlock.events, null, 2)}</pre>
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};

export default BlockDetails; 