import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import {
  Box,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  CircularProgress,
} from '@mui/material';
import { fetchRecentBlocks } from '../store/slices/blocksSlice';
import { AppDispatch, RootState } from '../store/store';

const Home: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();
  const { recentBlocks, loading, error } = useSelector((state: RootState) => state.blocks);

  useEffect(() => {
    dispatch(fetchRecentBlocks(10));
  }, [dispatch]);

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="200px">
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box p={3}>
        <Typography color="error">{error}</Typography>
      </Box>
    );
  }

  return (
    <Box p={3}>
      <Typography variant="h4" gutterBottom>
        Recent Blocks
      </Typography>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Block Number</TableCell>
              <TableCell>Hash</TableCell>
              <TableCell>Timestamp</TableCell>
              <TableCell>Author</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {recentBlocks.map((block) => (
              <TableRow key={block.number}>
                <TableCell>
                  <Link to={`/blocks/${block.number}`}>{block.number}</Link>
                </TableCell>
                <TableCell>{block.hash}</TableCell>
                <TableCell>{new Date(block.timestamp).toLocaleString()}</TableCell>
                <TableCell>{block.author}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default Home; 