import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import {
  Box,
  Paper,
  Typography,
  TextField,
  Button,
  Grid,
  FormControlLabel,
  Checkbox,
} from '@mui/material';
import { searchBlocks } from '../store/slices/blocksSlice';
import { AppDispatch } from '../store/store';

interface SearchParams {
  blockNumber: string;
  hash: string;
  author: string;
  finalized: boolean;
}

const Search: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();
  const [searchParams, setSearchParams] = useState<SearchParams>({
    blockNumber: '',
    hash: '',
    author: '',
    finalized: false,
  });

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    // Filter out empty parameters
    const params = Object.fromEntries(
      Object.entries(searchParams).filter(([_, value]) => 
        value !== '' && value !== false
      )
    );
    dispatch(searchBlocks(params));
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, checked } = e.target;
    setSearchParams((prev) => ({
      ...prev,
      [name]: name === 'finalized' ? checked : value,
    }));
  };

  return (
    <Box component="form" onSubmit={handleSearch} sx={{ maxWidth: 600, mx: 'auto' }}>
      <Typography variant="h4" gutterBottom>
        Search Blocks
      </Typography>
      <Paper sx={{ p: 3 }}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Block Number"
              name="blockNumber"
              value={searchParams.blockNumber}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Block Hash"
              name="hash"
              value={searchParams.hash}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Author"
              name="author"
              value={searchParams.author}
              onChange={handleChange}
            />
          </Grid>
          <Grid item xs={12}>
            <FormControlLabel
              control={
                <Checkbox
                  name="finalized"
                  checked={searchParams.finalized}
                  onChange={handleChange}
                />
              }
              label="Finalized Only"
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              fullWidth
              size="large"
            >
              Search
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};

export default Search; 