import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import axios from 'axios';

interface Block {
  number: number;
  hash: string;
  parentHash: string;
  timestamp: number;
  author: string;
  extrinsics: any[];
  events: any[];
}

interface SearchParams {
  blockNumber?: string;
  hash?: string;
  author?: string;
  finalized?: boolean;
}

interface BlocksState {
  recentBlocks: Block[];
  currentBlock: Block | null;
  loading: boolean;
  error: string | null;
}

const initialState: BlocksState = {
  recentBlocks: [],
  currentBlock: null,
  loading: false,
  error: null,
};

export const fetchRecentBlocks = createAsyncThunk<Block[], number>(
  'blocks/fetchRecentBlocks',
  async (limit = 10) => {
    const response = await axios.get(`/api/blocks/recent?limit=${limit}`);
    return response.data;
  }
);

export const fetchBlock = createAsyncThunk<Block, string>(
  'blocks/fetchBlock',
  async (blockNumber: string) => {
    const response = await axios.get(`/api/blocks/${blockNumber}`);
    return response.data;
  }
);

export const searchBlocks = createAsyncThunk<Block[], SearchParams>(
  'blocks/searchBlocks',
  async (params) => {
    const response = await axios.get('/api/blocks/search', { params });
    return response.data;
  }
);

const blocksSlice = createSlice({
  name: 'blocks',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchRecentBlocks.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(fetchRecentBlocks.fulfilled, (state, action: PayloadAction<Block[]>) => {
        state.loading = false;
        state.recentBlocks = action.payload;
      })
      .addCase(fetchRecentBlocks.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message || 'Failed to fetch recent blocks';
      })
      .addCase(fetchBlock.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(fetchBlock.fulfilled, (state, action: PayloadAction<Block>) => {
        state.loading = false;
        state.currentBlock = action.payload;
      })
      .addCase(fetchBlock.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message || 'Failed to fetch block';
      })
      .addCase(searchBlocks.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(searchBlocks.fulfilled, (state, action: PayloadAction<Block[]>) => {
        state.loading = false;
        state.recentBlocks = action.payload;
      })
      .addCase(searchBlocks.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message || 'Failed to search blocks';
      });
  },
});

export default blocksSlice.reducer; 